/* ==========================================================================
   WooCommerce Cart page responsive styles - begin
   ========================================================================== */

@include laptop-landscape-mac {

	.eltdf-woocommerce-page {

		.woocommerce {
			.woocommerce-cart-form {
				width: 61%;
			}

			.cart-collaterals {
				width: 39%;
			}
		}
	}

    .eltdf-woocommerce-page {
        .eltdf-page-content-holder:not(:last-child) {
            .woocommerce {
                .woocommerce-cart-form {
                    width: 100%;
                }

                .cart-collaterals {
                    width: 100%;
                }
            }
        }
    }
}

@include ipad-landscape {

	.eltdf-woocommerce-page {

		.woocommerce {
			.woocommerce-cart-form {
				width: 100%;
				padding: 0;
			}

			.cart-collaterals {
				width: 100%;
			}
		}
	}
}

@include ipad-portrait {

	.eltdf-quantity-input{
		padding-right: 20px !important;
	}

	.eltdf-woocommerce-page {

		table.cart {

			td.actions {

				.coupon {

					input[type="text"] {
						width: 200px;
					}
				}
			}
		}
	}
}

@include phone-landscape {

	.eltdf-quantity-input{
		padding-right: 20px !important;
	}

	.eltdf-woocommerce-page {

		.woocommerce {
			table.cart {

				tr {

					th, td {
						font-size: 12px;
					}

					&.cart_item {

						td {

							&.product-thumbnail {
								width: auto;
								padding-right: 0;
								display: none;

								img {
									display: none;
								}
							}

							&.product-name {
								padding-right: 10px;
							}

							&.product-remove{
								padding-right: 0;
							}
						}
					}
				}

				td.actions {

					.coupon {
						margin: 0 0 20px;

						input[type="text"] {
							width: 100%;
						}

						input[type="submit"] {
							right: 0;
						}
					}

					.coupon,
					> input {
						float: none;
						display: block;
					}
				}
			}
		}

	}
}

@include phone-portrait {

	.eltdf-woocommerce-page {

		.woocommerce {

			table.cart {

				td.actions {

					button[type="submit"] {
						padding: 0;
						margin-top: 6px;
					}
				}
			}

			.cart-collaterals {

				.woocommerce-shipping-calculator {

					p>button {
						width: 100%;
						font-size: 14px;
						padding: 15px 26px;
					}
				}

				.wc-proceed-to-checkout a {
					padding: 15px 26px;
				}

				table {

					tr.shipping td {
						width: 89%;
						text-align: left;
					}

					th {
						text-align: left;
						display: inline-block;
					}

					td,
					th {
						display: inline-block;
						padding-left: 30px;
					}

					tr.cart-subtotal th,
					tr.cart-subtotal td {
						display: inline-block;
						width: 50%;
						box-sizing: border-box;
						text-align: right;
					}

					td[data-title="Total"] {
						margin: 0;
						padding: 0;
					}

					tr.order-total {
						box-sizing: border-box;
					}

					tr.order-total th,
					tr.order-total td {
						width: 50%;
						box-sizing: border-box;
					}
				}
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Cart page responsive styles - end
   ========================================================================== */