@media screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape-large) {
	.eltdf-product-single .eltdf-sp-text-wrapper {
		bottom: -37px;
	}
}

@include ipad-landscape {
	.eltdf-product-single {
		.eltdf-product-single-content {
			display: block;
		}

		.eltdf-product-single-image {
			display: block;
		    height: auto;
		    width: 100%;
		}

		.eltdf-product-single-text {
		    display: block;
		    height: auto;
		    width: 100%;
		    margin: 50px 0;
		    padding: 0 0 0 50px;

		    .eltdf-product-single-custom-title {
		    	font-size: 40px;
		    }

		    &.eltdf-single-product-position-right  {
		    	padding: 0 50px 0 0;

		    	.eltdf-product-single-custom-subtitle {
		    		padding-top: 70px;
		    	}
		    }
		}

		&.eltdf-product-text-outside {
			&.eltdf-product-single-featured-left,
			&.eltdf-product-single-featured-right {
				.eltdf-sp-text-wrapper {
					position: relative;
					left: auto;
					right: auto;
					bottom: auto;
					margin: 0;
					padding: 15px;
                    text-align: left;

					.eltdf-product-single-title {
						width: calc(100% - 80px);
						float: left;
						display: block;
						margin: 0;
					}

					.eltdf-sp-price {
                        width: calc(100% - 80px);
						float: left;
						display: block;
						margin: 10px 0 0;
						clear: none;
					}

					.eltdf-sp-button {
						position: absolute;
						z-index: 2;
						display: inline-block;
						margin: 0;
						right: 15px;
						top: 15px;
                        width: 80px;
					}
				}
			}
		}

	} 

	.rev_slider_wrapper .eltdf-product-single .eltdf-product-single-image {
		display: none;
	}

	.rev_slider_wrapper .eltdf-product-single {
		.eltdf-product-single-text {
			padding: 0;
			margin: 0;

			&.eltdf-single-product-position-right {
				padding: 0;
			}
		}

		.eltdf-sp-text-wrapper {
			padding: 0 15px 15px 15px;
			box-sizing: border-box;
		}

		.eltdf-rating-holder {
			right: 15px;
		}
	}
}

@include phone-landscape {
	.eltdf-product-single {
		.eltdf-product-single-text {
			padding: 0 15px;

			&.eltdf-single-product-position-right {
				padding: 0 15px;
				
				.eltdf-sp-excerpt {
					margin: 0;
				}

			}

			.eltdf-sp-excerpt {
				margin: 0;
			}
		}
	}
}